.eum_day_number_wrapper {
  display: inline-block;
}

.eum_logger_additional_options {
  display: none;
}

#logs-clearing, #delay-updates {
  width: 80px;
}

#anonymize-updates-wrapper label {
  display: block;
  margin-bottom: 5px;
}

#save-anonymize-update-option {
  margin-top: 15px;
}
#log-export {
  position: relative;
}
.export-date-range {
	width: 400px;
	position: absolute;
	z-index: 1001;
  background: #FFF;
}
.export-date-range {
	border: 1px solid #ccc;
	padding: 15px;
	background: #fff;
	box-shadow: 0px 5px 20px rgba(0,0,0,.2);
	text-align: center;
}
.export-date-range .export-button .button-secondary {
  display: inline-block;
  margin: 0;
}
.export-date-range label {
    font-size: 12px;
    color: #333;
    font-weight: bold;
    display: block;
    padding: 0 0 4px 0;
    text-align: left;
    text-shadow: none;
}
.export-date-range .eum-export-date {
    display: inline-block;
    width: 150px;
    background: #fff;
    font-size: 12px;
    color: #333;
    max-width: none;
    padding: 1px 10px;
}
#TB_ajaxWindowTitle {
  text-align: center;
}
#TB_window {
  background: transparent !important;
  box-shadow: none !important;
}
#TB_ajaxContent {
	background: #fff;
}
.export-table {
  margin-bottom: 90px;
}
form#form-export-options {
  margin: 0;
}
#export-options {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 30px;
  bottom: 0;
  padding: 20px 10px;
  background-color: #0a8849;
  color: #FFF;

  .button,
  .button-primary,
  .button-secondary {
      display: inline-block;
      text-decoration: none;
      font-size: 13px;
      line-height: 26px;
      height: 28px;
      margin: 0;
      padding: 0 10px 1px;
      cursor: pointer;
      border-width: 1px;
      border-style: solid;
      -webkit-appearance: none;
      border-radius: 3px;
      white-space: nowrap;
      box-sizing: border-box;
  }
}
.export-table thead,
.export-table tfoot {
  tr {
    background: #333;
    th {
      color: #FFF;
    }
  }
}
.export-logs h3 {
  text-align: center;
}
#select_days_list {
  display: inline;
}
#select_days_list .select2-container {
  max-width: 500px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  position: relative;
  vertical-align: middle;
}
#select_days_list .select2-selection {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  /*
  font-size: 14px;
  line-height: 2;
  */
  border-radius: 4px;
  min-height: 32px;
  border: 1px solid #8c8f94;
  -webkit-box-shadow: 0 0 0 transparent;
  box-shadow: 0 0 0 transparent;
  background-color: #fff;
  color: #2c3338;
  padding-bottom: 5px;
  padding-right: 5px;
  display: block;
  cursor: text;
  position: relative;
  -webkit-user-select: none;
}
#select_days_list label {
  margin-left: 4px;
}
#select_days_list .select2-selection ul {
  display: inline;
  list-style: none;
  padding: 0;
}
#select_days_list .select2-selection ul li.select2-selection__choice {
  background-color: #e4e4e4;
  border: 1px solid #aaa;
  border-radius: 4px;
  box-sizing: border-box;
  display: inline-block;
  margin-left: 5px;
  margin-top: 5px;
  padding: 0;
    padding-left: 0px;
  padding-left: 20px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
  list-style: none;
  cursor: text;
  margin-bottom: 0px;
  white-space: nowrap;
}
#select_days_list .select2-selection ul li button.select2-selection__choice__remove {
  background-color: transparent;
  border: none;
  border-right: 1px solid #aaa;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  color: #999;
  cursor: pointer;
  font-size: 1em;
  font-weight: bold;
  padding: 0 4px;
  position: absolute;
  left: 0;
  top: 0;
}
#select_days_list .select2-selection ul li button.select2-selection__choice__remove span {
  color: #999;
  cursor: pointer;
  font-size: 1em;
  font-weight: bold;
}
#select_days_list .select2-selection ul li span.select2-selection__choice__display {
  cursor: default;
  padding-left: 2px;
  padding-right: 5px;
}
#select_days_list .select2-selection ul li button.select2-selection__choice__remove:hover,
#select_days_list .select2-selection ul li button.select2-selection__choice__remove:focus {
  background-color: #f1f1f1;
  color: #333;
  outline: none;
}
#select_days_list .select2-selection ul li button.select2-selection__choice__remove span:hover,
#select_days_list .select2-selection ul li button.select2-selection__choice__remove span:focus {
  color: #333;
}